import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { createUserWithEmailAndPassword, updateProfile, signInWithEmailAndPassword } from 'firebase/auth';
import { auth, db } from '../firebase/firebaseConfig';
import { toast } from 'react-hot-toast';
import Layout from '../components/common/Layout';
import { doc, setDoc, getDoc } from 'firebase/firestore';
import { signInWithGoogle, getGoogleRedirectResult } from '../firebase/firebaseConfig';
import { FcGoogle } from 'react-icons/fc';
import { UserProfile } from '../types';
import { useAuth } from '../contexts/AuthContext';

const RegisterPage: React.FC = () => {
  const navigate = useNavigate();
  const { isAuthenticated, currentUser } = useAuth(); // AuthContext   
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [displayName, setDisplayName] = useState('');
  const [loading, setLoading] = useState(false);
  const [debugInfo, setDebugInfo] = useState<string>(''); //  

  //    
  useEffect(() => {
    console.log('Authentication state changed:', isAuthenticated, currentUser?.uid);
    setDebugInfo(prev => `${prev}\n : ${isAuthenticated ? '' : ' '}\n ID: ${currentUser?.uid || 'none'}`);
    
    //     
    if (isAuthenticated && currentUser) {
      toast.success(' .   .');
      //      
      setTimeout(() => navigate('/profile'), 2000);
    }
  }, [isAuthenticated, currentUser, navigate]);

  //    
  useEffect(() => {
    const handleRedirectResult = async () => {
      try {
        setDebugInfo('Google   ...');
        const result = await getGoogleRedirectResult();
        
        if (result && result.user) {
          //     
          console.log('Google   :', result.user);
          setDebugInfo(prev => `${prev}\nGoogle  : ${result.user.uid}`);
          
          // Firestore      
          const userDocRef = doc(db, 'users', result.user.uid);
          const userDoc = await getDoc(userDocRef);
          
          if (!userDoc.exists()) {
            //      (  )
            const userProfileData = {
              uid: result.user.uid,
              displayName: result.user.displayName,
              email: result.user.email,
              photoURL: result.user.photoURL,
            } as UserProfile;
            
            await setDoc(userDocRef, userProfileData);
            toast.success('Google   .');
            setDebugInfo(prev => `${prev}\nFirestore    `);
          }
          
          //  navigate       useEffect 
        } else {
          setDebugInfo(prev => `${prev}\nGoogle   `);
        }
      } catch (error: any) {
        console.error('   :', error);
        toast.error('Google     .');
        setDebugInfo(prev => `${prev}\n: ${error.message || '   '}`);
        setLoading(false);
      }
    };

    handleRedirectResult();
  }, []);

  const handleGoogleSignUp = async () => {
    setLoading(true);
    setDebugInfo('Google  ...');
    try {
      await signInWithGoogle();
      //    
      //   useEffect handleRedirectResult 
    } catch (error: any) {
      console.error('Google  :', error);
      toast.error('Google     .');
      setDebugInfo(prev => `${prev}\n: ${error.message || '   '}`);
      setLoading(false);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (password !== confirmPassword) {
      toast.error('  .');
      return;
    }

    if (password.length < 6) {
      toast.error(' 6  .');
      return;
    }

    try {
      setLoading(true);
      setDebugInfo('/  ...');
      
      // 
      const userCredential = await createUserWithEmailAndPassword(auth, email, password);
      setDebugInfo(prev => `${prev}\n : ${userCredential.user.uid}`);
      
      //   
      await updateProfile(userCredential.user, {
        displayName: displayName
      });
      setDebugInfo(prev => `${prev}\n  `);

      // Firestore    
      const userProfileData = {
        uid: userCredential.user.uid,
        displayName: displayName,
        email: userCredential.user.email,
        photoURL: userCredential.user.photoURL,
      } as UserProfile;
      
      await setDoc(doc(db, 'users', userCredential.user.uid), userProfileData);
      setDebugInfo(prev => `${prev}\nFirestore    `);

      toast.success(' .');
      
      //        
      await signInWithEmailAndPassword(auth, email, password);
      setDebugInfo(prev => `${prev}\n  `);
      
      //     useEffect  
    } catch (error: any) {
      console.error('Error registering user:', error);
      setDebugInfo(prev => `${prev}\n: ${error.message || '   '}`);
      
      if (error.code === 'auth/email-already-in-use') {
        toast.error('   .');
      } else {
        toast.error('   .');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <Layout>
      <div className="min-h-screen flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8">
        <div className="max-w-md w-full space-y-8">
          <div>
            <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900 dark:text-white">
              
            </h2>
            <p className="mt-2 text-center text-sm text-gray-600 dark:text-gray-400">
                ?{' '}
              <button
                onClick={() => navigate('/login')}
                className="font-medium text-blue-600 hover:text-blue-500 dark:text-blue-400"
              >
                
              </button>
            </p>
          </div>
          
          {/*   */}
          {debugInfo && (
            <div className="mt-4 p-3 bg-gray-100 dark:bg-gray-800 rounded text-xs text-gray-600 dark:text-gray-400 whitespace-pre-line">
              <h3 className="font-bold mb-1"> :</h3>
              {debugInfo}
            </div>
          )}
          
          {/*    */}
          <div className="mt-6">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300 dark:border-gray-700"></div>
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-white dark:bg-gray-900 text-gray-500 dark:text-gray-400">
                   
                </span>
              </div>
            </div>
            
            <div className="mt-6">
              <button
                type="button"
                onClick={handleGoogleSignUp}
                disabled={loading}
                className="w-full flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm bg-white hover:bg-gray-50 text-sm font-medium text-gray-700 dark:bg-gray-800 dark:border-gray-700 dark:text-white dark:hover:bg-gray-700"
              >
                <FcGoogle className="h-5 w-5 mr-2" />
                Google 
              </button>
            </div>
          </div>
          
          <div className="mt-6">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300 dark:border-gray-700"></div>
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-white dark:bg-gray-900 text-gray-500 dark:text-gray-400">
                    
                </span>
              </div>
            </div>
          </div>
          
          <form className="mt-8 space-y-6" onSubmit={handleSubmit}>
            <div className="rounded-md shadow-sm -space-y-px">
              <div>
                <label htmlFor="display-name" className="sr-only">
                  
                </label>
                <input
                  id="display-name"
                  name="displayName"
                  type="text"
                  required
                  value={displayName}
                  onChange={(e) => setDisplayName(e.target.value)}
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-t-md focus:outline-none focus:ring-blue-500 focus:border-blue-500 focus:z-10 sm:text-sm dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                  placeholder=""
                />
              </div>
              <div>
                <label htmlFor="email-address" className="sr-only">
                   
                </label>
                <input
                  id="email-address"
                  name="email"
                  type="email"
                  autoComplete="email"
                  required
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-blue-500 focus:border-blue-500 focus:z-10 sm:text-sm dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                  placeholder=" "
                />
              </div>
              <div>
                <label htmlFor="password" className="sr-only">
                  
                </label>
                <input
                  id="password"
                  name="password"
                  type="password"
                  autoComplete="new-password"
                  required
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-blue-500 focus:border-blue-500 focus:z-10 sm:text-sm dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                  placeholder=""
                />
              </div>
              <div>
                <label htmlFor="confirm-password" className="sr-only">
                   
                </label>
                <input
                  id="confirm-password"
                  name="confirmPassword"
                  type="password"
                  autoComplete="new-password"
                  required
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-b-md focus:outline-none focus:ring-blue-500 focus:border-blue-500 focus:z-10 sm:text-sm dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                  placeholder=" "
                />
              </div>
            </div>

            <div>
              <button
                type="submit"
                disabled={loading}
                className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? (
                  <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                    <svg
                      className="animate-spin h-5 w-5 text-white"
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                    >
                      <circle
                        className="opacity-25"
                        cx="12"
                        cy="12"
                        r="10"
                        stroke="currentColor"
                        strokeWidth="4"
                      ></circle>
                      <path
                        className="opacity-75"
                        fill="currentColor"
                        d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                      ></path>
                    </svg>
                  </span>
                ) : null}
                {loading ? ' ...' : ''}
              </button>
            </div>
          </form>
        </div>
      </div>
    </Layout>
  );
};

export default RegisterPage; 