import React, { useState, useEffect } from 'react';
import Button from '../common/Button';
import Card from '../common/Card';
import { useAuth } from '../../contexts/AuthContext';
import { WorkoutGuideInfo, UserProfile, SetConfiguration } from '../../types';
import { toast } from 'react-hot-toast';
import { useWorkoutSettings, WorkoutSettings } from '../../hooks/useWorkoutSettings';

// guideInfo   
const getInitialGuideInfo = (userProfile: any, settings: WorkoutSettings): WorkoutGuideInfo => {
  return {
    gender: userProfile?.gender || 'male',
    age: userProfile?.age || 30,
    weight: userProfile?.weight || 70,
    experience: userProfile?.experience?.level || 'beginner',
    trainingYears: userProfile?.experience?.years || 0,
    oneRepMaxes: {
      squat: userProfile?.oneRepMax?.squat || 0,
      deadlift: userProfile?.oneRepMax?.deadlift || 0,
      bench: userProfile?.oneRepMax?.bench || 0,
      overheadPress: userProfile?.oneRepMax?.overheadPress || 0,
    },
    preferredSetConfig: settings?.preferredSetup || '10x5',
  };
};

interface WorkoutSetConfigProps {
  onConfigSaved?: () => void;
}

const WorkoutSetConfig: React.FC<WorkoutSetConfigProps> = ({ onConfigSaved }) => {
  const { currentUser, userProfile } = useAuth();
  const { settings, updateSettings, isLoading, isUpdating } = useWorkoutSettings();
  
  //  guideInfo 
  const [guideInfo, setGuideInfo] = useState<WorkoutGuideInfo>(() => 
    getInitialGuideInfo(userProfile, settings || { 
      preferredSetup: '10x5' as SetConfiguration, 
      customSets: 5, 
      customReps: 10 
    })
  );
  
  //   guideInfo 
  useEffect(() => {
    if (settings) {
      setGuideInfo(prev => ({
        ...prev,
        preferredSetConfig: settings.preferredSetup
      }));
    }
  }, [settings]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    
    if (name.includes('.')) {
      const [parent, child] = name.split('.');
      setGuideInfo((prev: WorkoutGuideInfo) => {
        const parentObj = prev[parent as keyof WorkoutGuideInfo] || {};
        //     
        const parentValue = typeof parentObj === 'object' && parentObj !== null ? parentObj : {};
        
        return {
          ...prev,
          [parent]: {
            ...parentValue,
            [child]: name.includes('oneRepMaxes') ? Number(value) : value
          }
        };
      });
    } else {
      setGuideInfo((prev: WorkoutGuideInfo) => ({
        ...prev,
        [name]: name === 'age' || name === 'weight' || name === 'trainingYears' ? Number(value) : value
      }));
    }
  };

  //     (React Query  )
  const applyToProfile = async () => {
    if (!currentUser) return;
    
    try {
      console.log('   :', guideInfo.preferredSetConfig);
      
      //         
      let customSets = 5;
      let customReps = 10;
      
      //         
      const setConfig = guideInfo.preferredSetConfig as SetConfiguration;
      
      switch (setConfig) {
        case '5x5':
          // 5 5 (- )
          customSets = 5;
          customReps = 5;
          break;
        case '6x3':
          // 6 3 (  -  )
          customSets = 3;
          customReps = 6;
          break;
        case '10x5':
          // 10 5 (- )
          customSets = 5;
          customReps = 10;
          break;
        case '15x5':
          // 15 5 (  )
          customSets = 5;
          customReps = 15;
          break;
        default:
          // : 10 5
          customSets = 5;
          customReps = 10;
      }
      
      //   
      const newSettings: WorkoutSettings = {
        preferredSetup: setConfig,
        customSets,
        customReps
      };
      
      console.log('  :', newSettings);
      
      // React Query    
      updateSettings(newSettings);
      
      //    
      toast.success(`  ${setConfig} ! (${customReps} ${customSets})`, {
        duration: 3000,
        position: 'top-center',
        icon: ''
      });
      
      //    -     
      if (userProfile?.experience) {
        //      
        const profileUpdate = {
          experience: {
            ...userProfile.experience,
            level: guideInfo.experience
          }
        };
        
        //       .
      }
      
      //   
      if (onConfigSaved) {
        onConfigSaved();
      }
    } catch (error) {
      console.error('    :', error);
      //   React Query  
    }
  };

  //   
  if (isLoading) {
    return (
      <Card className="p-6">
        <div className="flex justify-center items-center h-40">
          <div className="animate-spin rounded-full h-10 w-10 border-b-2 border-blue-500"></div>
        </div>
      </Card>
    );
  }

  return (
    <Card className="p-6">
      <h2 className="text-xl font-bold mb-6">   </h2>
      
      <div className="space-y-6">
        <div>
          <label className="flex items-center p-4 border rounded-md mb-2 cursor-pointer hover:bg-gray-50 dark:hover:bg-gray-700">
            <input
              type="radio"
              name="preferredSetConfig"
              value="5x5"
              checked={guideInfo.preferredSetConfig === '5x5'}
              onChange={handleInputChange}
              className="mr-3"
            />
            <div>
              <h3 className="font-medium">5x5 (5 5)</h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">  </p>
            </div>
          </label>
          
          <div className="ml-6 text-sm text-gray-600 dark:text-gray-400 mt-2">
            <ul className="list-disc ml-5 space-y-1">
              <li>    </li>
              <li>    </li>
              <li>      </li>
              <li>     </li>
              <li>  (, ,  ) </li>
            </ul>
          </div>
        </div>
        
        <div>
          <label className="flex items-center p-4 border rounded-md mb-2 cursor-pointer hover:bg-gray-50 dark:hover:bg-gray-700">
            <input
              type="radio"
              name="preferredSetConfig"
              value="10x5"
              checked={guideInfo.preferredSetConfig === '10x5'}
              onChange={handleInputChange}
              className="mr-3"
            />
            <div>
              <h3 className="font-medium">10x5 (10 5)</h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">- </p>
            </div>
          </label>
          
          <div className="ml-6 text-sm text-gray-600 dark:text-gray-400 mt-2">
            <ul className="list-disc ml-5 space-y-1">
              <li>(muscle hypertrophy)  </li>
              <li>    </li>
              <li>  </li>
              <li> (metabolic stress)     </li>
            </ul>
          </div>
        </div>
        
        <div>
          <label className="flex items-center p-4 border rounded-md mb-2 cursor-pointer hover:bg-gray-50 dark:hover:bg-gray-700">
            <input
              type="radio"
              name="preferredSetConfig"
              value="6x3"
              checked={guideInfo.preferredSetConfig === '6x3'}
              onChange={handleInputChange}
              className="mr-3"
            />
            <div>
              <h3 className="font-medium">6x3 (6 3)</h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">  -  </p>
            </div>
          </label>
          
          <div className="ml-6 text-sm text-gray-600 dark:text-gray-400 mt-2">
            <ul className="list-disc ml-5 space-y-1">
              <li>    </li>
              <li>     </li>
              <li>        </li>
              <li>    (   )</li>
              <li>  (fast-twitch fiber)  </li>
            </ul>
          </div>
        </div>
        
        <div>
          <label className="flex items-center p-4 border rounded-md mb-2 cursor-pointer hover:bg-gray-50 dark:hover:bg-gray-700">
            <input
              type="radio"
              name="preferredSetConfig"
              value="15x5"
              checked={guideInfo.preferredSetConfig === '15x5'}
              onChange={handleInputChange}
              className="mr-3"
            />
            <div>
              <h3 className="font-medium">15x5 (15 5)</h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">  </p>
            </div>
          </label>
          
          <div className="ml-6 text-sm text-gray-600 dark:text-gray-400 mt-2">
            <ul className="list-disc ml-5 space-y-1">
              <li>  </li>
              <li>  </li>
              <li>        </li>
              <li> (slow-twitch fiber)  </li>
              <li>  </li>
            </ul>
          </div>
        </div>
      </div>
      
      <div className="flex justify-end mt-6">
        <Button 
          variant="primary" 
          onClick={applyToProfile}
          disabled={isUpdating}
        >
          {isUpdating ? (
            <>
              <span className="animate-spin mr-2"></span>
               ...
            </>
          ) : ''}
        </Button>
      </div>
    </Card>
  );
};

export default WorkoutSetConfig; 