import { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { User, onAuthStateChanged } from 'firebase/auth';
import { doc, getDoc, setDoc, updateDoc } from 'firebase/firestore';
import { auth, db, getUserProfile, updateUserProfile as updateFirebaseProfile } from '../firebase/firebaseConfig';
import { UserProfile, UserSettings } from '../types';
import { 
  calculateBMR, 
  calculateNutritionGoals,
  activityMultipliers, 
  goalMultipliers,
  ActivityLevel,
  FitnessGoal
} from '../utils/nutritionUtils';

interface AuthContextType {
  currentUser: User | null;
  userProfile: UserProfile | null;
  userSettings: UserSettings | null;
  loading: boolean;
  error: string | null;
  updateProfile: (profile: Partial<UserProfile>) => Promise<void>;
  updateSettings: (settings: Partial<UserSettings>) => Promise<void>;
  logout: () => Promise<void>;
  isAuthenticated: boolean;
}

const defaultProfile: UserProfile = {
  uid: '',
  displayName: null,
  email: null,
  photoURL: null,
  height: 170,
  weight: 70,
  age: 25,
  gender: 'male',
  activityLevel: 'moderate',
  fitnessGoal: 'maintain',
  experience: {
    years: 0,
    level: 'beginner',
    squat: {
      maxWeight: 0,
      maxReps: 0
    }
  }
};

const defaultSettings: UserSettings = {
  darkMode: false,
  notifications: {
    workoutReminder: true,
    mealReminder: true,
    progressUpdate: true
  },
  units: {
    weight: 'kg',
    height: 'cm'
  },
  language: 'ko'
};

const AuthContext = createContext<AuthContextType | undefined>(undefined);

//  helper  
const isObject = (item: any) => {
  return (item && typeof item === 'object' && !Array.isArray(item));
};

const deepMerge = (target: any, source: any) => {
  const output = Object.assign({}, target);
  
  if (isObject(target) && isObject(source)) {
    Object.keys(source).forEach(key => {
      if (isObject(source[key])) {
        if (!(key in target)) {
          Object.assign(output, { [key]: source[key] });
        } else {
          output[key] = deepMerge(target[key], source[key]);
        }
      } else {
        Object.assign(output, { [key]: source[key] });
      }
    });
  }
  
  return output;
};

//     
const checkPersonalizationNeeded = async (uid: string) => {
  try {
    const userDocRef = doc(db, 'users', uid);
    const userDoc = await getDoc(userDocRef);
    
    //        
    if (!userDoc.exists() || 
        !userDoc.data().height || 
        !userDoc.data().weight ||
        !userDoc.data().setConfiguration) {
      return true;
    }
    return false;
  } catch (error) {
    console.error('     :', error);
    return true; //     
  }
};

// Firestore    undefined   
const removeUndefined = (obj: any): any => {
  if (obj === undefined) return null;
  
  if (typeof obj !== 'object' || obj === null) return obj;
  
  if (Array.isArray(obj)) {
    return obj.map(item => removeUndefined(item));
  }
  
  const result: any = {};
  for (const key in obj) {
    const value = removeUndefined(obj[key]);
    if (value !== undefined) {
      result[key] = value;
    }
  }
  
  return result;
};

//     nutritionUtils     
const calculateTargetCaloriesInContext = (
  height: number,
  weight: number,
  age: number,
  gender: 'male' | 'female',
  activityLevel: ActivityLevel, //  
  fitnessGoal: FitnessGoal      //  
): number => {
  const bmr = calculateBMR(gender, weight, height, age); // nutritionUtils calculateBMR 
  const tdee = bmr * (activityMultipliers[activityLevel] || activityMultipliers.moderate); // nutritionUtils activityMultipliers 
  
  let targetCalories = tdee;
  if (goalMultipliers[fitnessGoal]) { // nutritionUtils goalMultipliers 
    targetCalories = tdee * goalMultipliers[fitnessGoal];
  }
  return Math.round(targetCalories);
};

export function AuthProvider({ children }: { children: ReactNode }) {
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [userProfile, setUserProfile] = useState<UserProfile | null>(null);
  const [userSettings, setUserSettings] = useState<UserSettings | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      setCurrentUser(user);
      setIsAuthenticated(!!user);
      
      if (user) {
        try {
          //    
          const userDocRef = doc(db, 'users', user.uid);
          const settingsDocRef = doc(db, 'userSettings', user.uid);
          
          // Promise.all     
          const [profileDoc, settingsDoc] = await Promise.all([
            getDoc(userDocRef),
            getDoc(settingsDocRef)
          ]);
          
          //   
          if (profileDoc.exists()) {
            setUserProfile(profileDoc.data() as UserProfile);
          } else {
            //     
            const newProfile: UserProfile = {
              ...defaultProfile,
              uid: user.uid,
              displayName: user.displayName,
              email: user.email,
              photoURL: user.photoURL
            };
            
            // Firestore 
            await setDoc(userDocRef, newProfile);
            setUserProfile(newProfile);
          }
          
          //   
          if (settingsDoc.exists()) {
            setUserSettings(settingsDoc.data() as UserSettings);
          } else {
            //     
            await setDoc(settingsDocRef, defaultSettings);
            setUserSettings(defaultSettings);
          }
          
          //    
          const needsPersonalization = await checkPersonalizationNeeded(user.uid);
          if (needsPersonalization) {
            // PersonalizationModal   
            sessionStorage.setItem('needsPersonalization', 'true');
          }
        } catch (err) {
          console.error('Error fetching user data:', err);
          setError(err instanceof Error ? err.message : '     .');
        } finally {
          setLoading(false);
        }
      } else {
        //    
        setUserProfile(null);
        setUserSettings(null);
        setLoading(false);
      }
    });
    
    return unsubscribe;
  }, []);

  const updateProfile = async (profile: Partial<UserProfile>) => {
    if (!currentUser) return;
    
    try {
      setLoading(true);
      console.log('AuthContext:   ', profile);
      
      //    
      const userDocRef = doc(db, 'users', currentUser.uid);
      const userDoc = await getDoc(userDocRef);
      const previousData = userDoc.exists() ? userDoc.data() as UserProfile : null;
      
      console.log('AuthContext:   ', previousData);
      
      //    (   )
      const updatedProfile = deepMerge(
        previousData || defaultProfile,
        {
          ...profile,
          //      
          lastUpdated: new Date().toISOString()
        }
      );
      
      //    
      updatedProfile.uid = currentUser.uid;
      if (currentUser.displayName) updatedProfile.displayName = currentUser.displayName;
      if (currentUser.email) updatedProfile.email = currentUser.email;
      if (currentUser.photoURL) updatedProfile.photoURL = currentUser.photoURL;
      
      //     
      if (profile.height || profile.weight || profile.activityLevel || profile.gender || profile.age || profile.fitnessGoal) {
        const height = Number(updatedProfile.height) || defaultProfile.height;
        const weight = Number(updatedProfile.weight) || defaultProfile.weight;
        const age = Number(updatedProfile.age) || defaultProfile.age;
        const gender = updatedProfile.gender || defaultProfile.gender;
        // activityLevel fitnessGoal UserProfile   ActivityLevel, FitnessGoal   
        const activityLevelValue = updatedProfile.activityLevel || defaultProfile.activityLevel;
        const fitnessGoalValue = updatedProfile.fitnessGoal || defaultProfile.fitnessGoal;
        
        try {
          //  calculateTargetCaloriesInContext  
          const targetCalories = calculateTargetCaloriesInContext(
            height,
            weight,
            age,
            gender,
            activityLevelValue,
            fitnessGoalValue
          );
          
          if (!isNaN(targetCalories) && targetCalories > 0) {
            if (!profile.targetCalories) {
              updatedProfile.targetCalories = targetCalories;
            }
          }
        } catch (err) {
          console.error('     (AuthContext):', err);
        }
      }
      
      console.log('AuthContext:   ', updatedProfile);
      
      // undefined    (Firestore undefined    )
      const cleanProfile = removeUndefined(updatedProfile);
      console.log('AuthContext:    (Firestore  )', JSON.stringify(cleanProfile, null, 2));
      
      // Firestore     - merge  
      await setDoc(userDocRef, cleanProfile, { merge: true });
      console.log('AuthContext: Firestore  ');
      
      //   
      console.log('AuthContext: setUserProfile  , updatedProfile :', JSON.stringify(updatedProfile, null, 2));
      setUserProfile(updatedProfile);
      // setUserProfile  ,    userProfile      .
      //      .
      console.log('AuthContext: setUserProfile   (   updatedProfile  )');
      
      //   -     
      window.dispatchEvent(new CustomEvent('userProfileUpdated', { 
        detail: { profile: updatedProfile } 
      }));
      
    } catch (err) {
      console.error('Error updating profile:', err);
      setError(err instanceof Error ? err.message : '    .');
    } finally {
      setLoading(false);
    }
  };

  const updateSettings = async (settings: Partial<UserSettings>) => {
    if (!currentUser) return;
    
    try {
      setLoading(true);
      
      const updatedSettings: UserSettings = {
        ...(userSettings || defaultSettings),
        ...settings
      };
      
      await updateDoc(doc(db, 'userSettings', currentUser.uid), settings);
      
      setUserSettings(updatedSettings);
      setError(null);
    } catch (err) {
      console.error('Error updating settings:', err);
      setError(err instanceof Error ? err.message : '    .');
    } finally {
      setLoading(false);
    }
  };

  const logout = async () => {
    try {
      await auth.signOut();
      setCurrentUser(null);
      setUserProfile(null);
      setUserSettings(null);
      setIsAuthenticated(false);
    } catch (err) {
      console.error('Error signing out:', err);
      setError(err instanceof Error ? err.message : '   .');
    }
  };

  const value = {
    currentUser,
    userProfile,
    userSettings,
    loading,
    error,
    updateProfile,
    updateSettings,
    logout,
    isAuthenticated
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}
